html {
  background: rgb(236, 244, 255) ;
}

body {
  margin: 0;
  padding: 0px;
  font-family: Lato, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
  padding-bottom: 320px;
  background-image: url("/IeeMDB/static/media/Group 3.1b483f5f.svg");
  background-position: bottom;
  background-repeat: no-repeat;
}

body * {
  -webkit-transition: all 500ms ease-in-out;
  transition: all 500ms ease-in-out;
}


.container {
  width: 80%;
  margin: 0 auto;
 
}



nav {
  display: flex;
  align-items: center;
  width: 100%;
  padding-top: 10px;
  margin: 0 auto;
  justify-content: space-between;
  color: #111;
}

nav ul {
  list-style: none;
  padding: 0px;
  margin: 10px 0px;
}

nav ul li a {
  text-decoration: none;
  color: #111;
}

nav ul li {
  display: inline-block;
  margin-left: 10px;
}

nav ul:first-of-type li:first-of-type{
  font-weight: bold;
  font-size: 1.1rem;
}



header {
  position: relative;
}


header div {
  padding-top: 150px;
 
}

header div h1 {
  font-size: 75px;
}

header div p {
  width: 550px;
  font-size: 20px;
  line-height: 28px;
}

header img {
  position: absolute;
  z-index: -1;
  width: 1630px;
  right: -50%;
  top: -20px;
}

header div a {
  padding: 20px;
  display: block;
  margin-top: 70px;
  width: 170px;
  border-radius: 40px;
  color: #eee;
  font-size: 20px;
  text-align: center;
  text-decoration: none;
}

header div a, .newsletter input[type="submit"] {
  border: 1px solid blue;
  color: blue;
}

header div a:hover, .newsletter input[type="submit"]:hover {
  background: blue;
  color: #eee;  
}

main {
  padding-top: 100px;
}

h2 {
  font-size: 45px;
  margin-bottom: 120px;
  
}

h3 {
  font-size: 30px;
  color: rgb(12, 0, 116);
}



.services {
  margin-top: 300px;
}

.service-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  align-items: center;
  margin: 40px 0px 180px 0px;
  position: relative;
}


.services-card {
  width: 40%;
  height: 300px;
  background: #222;
  display: inline-block;  
  border-radius: 10px;
  box-shadow: 0px 0px 52px -19px rgba(0,0,0,0.75);
}

.service-description{
  width: 40%;

}

.service-one {
  background: url(/IeeMDB/static/media/movie.2a468ea8.png)center;
}

.service-two {
  background: url(/IeeMDB/static/media/actors.a9291e94.png)center;
}

.service-three {
  background: url("https://images.unsplash.com/photo-1427751840561-9852520f8ce8?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=755&q=80")center;
}


.newsletter {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}

.newsletter * {
  padding: 20px;
  border-radius: 40px;
  border: none;
  margin: 0px;
  background: none;
  font-size: 20px;

}

.newsletter input[type="email"] {
  width: 60%;
  display: inline-block;
  border: 1px solid #222;

  margin-right: 20px;

}

.newsletter input[type="submit"]{
  width: 170px;
  color: blue;
 
  cursor: pointer;
}

.thanks {
  font-size: 30px;
  width:70%;
  margin: 120px auto;
  text-align: center;
}

.contact {
  width: 60%;
  margin: 0 auto;
  max-width: 900px;
 
}

.contact * {
  width: 100%;
  padding: 20px 0px;
  display: block;
  border-radius: 10px;
}
.contact input, .contact textarea {
  text-indent: 30px;
  border: 1px solid #eee;
}

.contact input[type="submit"] {
  background: blue;
  width: 30%;
  text-indent: 0px;
  color: #eee;
  font-size: 20px;
  border-radius: 40px;
  margin: 40px 0px 0px auto;
}

.back {
  background: none;
  padding: 10px;
  border: none;
  border-bottom: 3px solid blue;
  font-size: 14px;
  margin-top: 50px;
  cursor: pointer;
  color: blue;
}

@media screen and (max-width: 1600px){
  header img {
    width: 1230px;
    right: -50%;
    top: -20px;
  }
}


@media screen and (max-width: 1380px){
  header img {
    right: -66%;
    top: -20px;
  }

  header div {
    padding-top: 100px;   
  }
  header div h1 {
    font-size: 55px;
  }
  
  header div p {
    width: 400px;
  }

}

@media screen and (max-width: 1150px){
  header img {
    width: 900px;
    right: -50%;
    top: 320px;
  }
 
  
  header div h1 {
    font-size: 55px;
  }
  
  header div p {
    width: 400px;
  }

  .service-container {
    display: block;
  }
  
  .services-card {
    width: 100%;
    height: 400px;
  }
  
  .service-description{
    width: 100%;
  
  }
 
  .service-one, .service-two, .service-three{
    background-size: 120% !important;
  }

  .contact input[type="submit"] {
    width: 40%;
  }
  
}

.imgd{
width: 750px;
height: 660px;
top: 35%;
left: 60%;
border-radius: 10px;
background: #222;
display: inline-block;
box-shadow: 0px 0px 52px -19px rgba(0,0,0,0.75);
}
@media screen and (max-width: 950px) {

  header img {
    width: 730px;
    right: -50%;
    top: 520px;
  }

  .newsletter {
    width: 100%;
  }
  .newsletter input[type="email"] {
    width: 100%;
    margin-right: 0px;
  
  }
  
  .newsletter input[type="submit"]{
    margin-top: 40px;
    
  }

  .contact {
    width: 90%;
  }
  
  .contact input[type="submit"] {
   
    width: 100%;
    margin: 40px 0px;
  }
  
}
